h2 {
    margin: 15px 0 0 0;
}
.testimonial .left {
    width:35%;
    padding: 35px;
    margin:20px;
}
.testimonial .right {
    width:65%;
    padding:0 0 0 50px;
    margin: 0 20px 0 0;
}
/* .testimonial .quote */
.testimonial .content {
    padding: 50px;
}
.testimonial .content h1 {
    font-weight: 500;
}
.testimonial .content h3 {
    font-weight: 400;
    font-style: 18px;
    margin-bottom: 40px;
}
.testimonial .slide-button {
    position: absolute;
    top:30px;
    right: 20px;
}
.testimonial .details span {
    color:#ff014f;
}
.testimonial .slide-button .prev-btn {
    margin-right: 30px;
}
/*---------------Testimonial---------------------------*/
.testimonial .slide {
    max-width: 80%;
    margin:auto;
    height:500px;
    position: relative;
    display: flex;
    overflow: hidden;
} 
 article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
}
.article .activeSlide {
    opacity: 1;
    transform: translateX(0);
}
/* .article .lastSlide {
    transform: translateX(-100%);
}
.article .nextSlide {
    transform: translateX(100%); */
/* } */
@media (max-width:768px) {
    .article{
        flex-direction: column;
        padding: 20px;
    }
    .testimonial .left {
        width:100%;
        display:flex;
        margin:0;
        margin-top: 15%;
    }
    .testimonial .left img {
        width: 190px;
        border-radius: 10px;
        margin-right: 20px;
    }
    .testimonial .right{
        width:100%;
        margin: 0;
        padding: 0 0 0 0;
    }
    
}