.resume {
    margin-left: 30px;
    margin-right: 10px;
}
.resume .heading h1{
    font-size: 40px;
}
.resume .content {
    max-width: 80%;
    margin: auto;
}
.resume .box {
    padding: 40px;
}
.resume hr {
    margin: 30px 0;
    border:1px solid #e339ed;
    background-color: #e3e9ed;
}
.resume .rate .btn-shadow{
    color:#ff014f;
    padding:15px 30px;
    font-weight: bold;
    transition: 0.5s;
}
.resume p {
    font-size: 18px;
    transition: 0.5s;
}
.resume .box {
    margin-bottom: 40px;
    position: relative;
}
.resume .btn-shadow:hover {
    transform: translateY(0);
    color:white;
}
.resume .box:hover {
    background-color: #ff014f;
    box-shadow: none;
    color:white;
}
.resume .box p {
    color:white;
}
.resume .content-section {
    position: relative;
}
.resume .left {
    position:relative;
}
.resume .left::after{
    content: '';
    position:absolute;
    top:16%;
    left:0;
    height:118vh;
    background: #d9dee2;
    z-index: -2;
}
.resume .box::after{
    content: '';
    position:absolute;
    top:13%;
    left:-8%;
    height:5px;
    width: 50px;
    background: #d9dee2;
    z-index: -2;
}
.resume .box::before{
    content: '';
    position:absolute;
    top:11%;
    left:-58px;
    height:10px;
    width: 10px;
    background: #d9dee2;
    z-index: -2;
    border: 2px solid #d9dee2;
    border-radius:50%;
    z-index:5;
}
.resume .box:hover.box::before {
    background: #ff014f;
}
@media (max-width:768px) {
    .resume .dflex {
        flex-direction: column;
    }
    .resume .left::after {
       top:14%;
       height: 105vh; 
    }
}